mponents/host/invitation/AddCity.vue"],"names":[],"mappings":"AAsLA,mCAUA,kBAAA,CANA,yBAAA,CAIA,YAAA,CANA,WAAA,CAOA,sBAAA,CAFA,MAAA,CAJA,cAAA,CAGA,KAAA,CALA,UAAA,CAIA,uBAMA,CACA,kCAEA,eAAA,CADA,qBAAA,CAEA,kDAAA,CAGA,eAAA,CADA,YAAA,CADA,UAGA,CACA,kCAGA,kBAAA,CAFA,YAAA,CACA,6BAEA,CACA,sCACA,UAAA,CAMA,YAAA,CACA,qBAAA,CANA,mBAAA,CACA,cAAA,CACA,iBAAA,CACA,eAAA,CACA,kBAGA,CACA,wDAKA,kBAAA,CAHA,aAAA,CAEA,UAAA,CAHA,cAAA,CAEA,UAGA,CACA,iCACA,UAAA,CACA,mBAAA,CACA,cAAA,CACA,iBAAA,CACA,eAAA,CACA,kBACA,CACA,sCACA,eACA,CACA,mFAIA,+BAAA,CAFA,yBAAA,CADA,iBAAA,CAKA,aAAA,CACA,mBAAA,CACA,cAAA,CACA,iBAAA,CACA,eAAA,CACA,gBAAA,CARA,YAAA,CAEA,UAOA,CACA,+FACA,+BACA,CACA,8CAKA,kBAAA,CAJA,WAAA,CAGA,kBAAA,CAGA,UAAA,CAMA,oBAAA,CALA,kBAAA,CACA,kBAAA,CACA,iBAAA,CACA,eAAA,CACA,kBAAA,CATA,kBAAA,CAWA,gBAAA,CAZA,YAAA,CAIA,iBASA,CACA,yBACA,SAAA,CAEA,aAAA,CADA,cAAA,CAEA,cACA,CACA,8BACA,YAAA,CACA,wBAAA,CACA,eACA,CACA,+BAGA,+BAAA,CADA,yBAAA,CADA,iBAGA","sourcesContent":["\n<template>\n    <div class=\"testi-popup-cont\">\n        <div class=\"testi-form-cont\">\n            <div class=\"testi-form-head\">\n                <div>\n                    <span>{{selectedForm.heading}}</span>\n                    <span></span>\n                </div>\n                <img style=\"cursor: pointer;\" @click=\"$emit('close')\" src=\"/assets/clarity_window-close-line.svg\" alt=\"\">\n            </div>\n            <p class=\"testi-form-que mt-3\">Your city <span>Details:</span></p>\n            <div v-if=\"edit == true\">\n                <div class=\"form-group\">\n                    <div class=\"multi-select\" style=\"padding: 14px 20px\">\n                        <multiselect\n                            v-model=\"selectedEdit\"\n                            placeholder=\"Select City\"\n                            :options=\"locations\"\n                            :show-labels=\"false\"\n                            :allow-empty=\"false\"\n                            @select=\"selectCityModel\"\n                            track-by=\"name\"\n                            label=\"name\"\n                            ><span slot=\"noResult\">No city exists.</span>\n                        </multiselect>\n                    </div>\n                </div>\n            </div>\n            <form>\n                <div class=\"mb-3\">\n                    <input type=\"text\" v-model=\"cityDetails.name\" name=\"name\" id=\"name\" placeholder=\"City Name\"></input>\n                    <span\n                        v-if=\"errorsSubmit && errorsSubmit.name\"\n                        class=\"errMsg\"\n                        >{{ errorsSubmit.name[0] }}</span\n                    >\n                </div>\n                <div class=\"mb-3\">\n                    <input type=\"text\" v-model=\"cityDetails.state\" name=\"state\" id=\"state\" placeholder=\"State\"></input>\n                    <span\n                        v-if=\"errorsSubmit && errorsSubmit.state\"\n                        class=\"errMsg\"\n                        >{{ errorsSubmit.state[0] }}</span\n                    >\n                </div>\n                <div class=\"d-flex justify-end\"><button type=\"button\" @click=\"addCity()\" :disabled=\"disableSubmit\">{{selectedForm.buttonText}}</button></div>\n                <div v-if=\"edit == false\" class=\"d-flex justify-end\"><button type=\"button\" @click=\"edit = true; selectedForm.heading = 'Edit'\">{{editBtn}}</button></div>\n                <div v-else class=\"d-flex justify-end\"><button type=\"button\" @click=\"undoEdit()\">Add New City</button></div>\n            </form>\n        </div>\n    </div>\n</template>\n\n<script>\nimport Multiselect from \"vue-multiselect\";\n\nexport default {\n    props: ['locations'],\n    components: {\n        Multiselect,\n    },\n    emits: ['close', 'city', 'message', 'all'],\n    data() {\n        return {\n            selectedForm: {\n                heading: 'Add Your City',\n                buttonText: 'Submit',\n            },\n            cityDetails: {\n                name: null,\n                state: null,\n            },\n            disableSubmit: false,\n            errorsSubmit: null,\n            editBtn: 'Edit Existing Cities',\n            edit: false,\n            editId: null,\n            selectedEdit: null,\n        }\n    },\n    methods: {\n        selectCityModel(n) {\n            this.cityDetails.name = n.name;\n            this.cityDetails.state = n.state || '';\n            this.editId = n.code;\n        },\n        undoEdit() {\n            this.edit = false;\n            this.selectedForm.heading = 'Add Your City';\n            this.cityDetails = {\n                name: null,\n                state: null,\n            };\n            this.selectedEdit = null;\n        },\n        addCity() {\n            let _this = this;\n            _this.disableSubmit = true;\n            _this.errorsSubmit = null;\n            \n            // Validate inputs\n            if (!_this.cityDetails.name || !_this.cityDetails.state) {\n                _this.errorsSubmit = {\n                    name: ['City name is required'],\n                    state: ['State is required']\n                };\n                _this.disableSubmit = false;\n                return;\n            }\n            \n            let formData = new FormData();\n            formData.append('name', _this.cityDetails.name);\n            formData.append('state', _this.cityDetails.state);\n            \n            // Log what we're sending\n            console.log('Sending data:', {\n                name: _this.cityDetails.name,\n                state: _this.cityDetails.state\n            });\n            \n            let url = '/api/host/cities';\n            let method = 'POST';\n            \n            if (_this.edit == true) {\n                url = `/api/host/cities/${_this.editId}`;\n                method = 'PUT';\n            }\n            \n            axios({\n                method: method,\n                url: url,\n                data: formData,\n                headers: { \n                    'Content-Type': 'multipart/form-data',\n                    'X-Requested-With': 'XMLHttpRequest'\n                }\n            })\n            .then((response) => {\n                console.log('Success response:', response.data);\n                _this.disableSubmit = false;\n                _this.message = response.data.message;\n                \n                if (_this.edit == true) {\n                    let updatedCity = {\n                        name: _this.cityDetails.name,\n                        state: _this.cityDetails.state,\n                        code: _this.editId\n                    };\n                    _this.$emit('city', [updatedCity, 'update']);\n                } else {\n                    let newCity = {\n                        name: _this.cityDetails.name,\n                        state: _this.cityDetails.state,\n                        code: response.data.data.id\n                    };\n                    _this.$emit('city', [newCity, 'add']);\n                }\n                \n                _this.$emit('message', response.data.message);\n                _this.$emit('all', response.data.all || _this.locations);\n                _this.$emit('close');\n            })\n            .catch((error) => {\n                _this.disableSubmit = false;\n                console.error('Error:', error);\n                console.error('Error response:', error.response);\n                \n                if (error.response && error.response.data) {\n                    console.error('Error data:', error.response.data);\n                    _this.errorsSubmit = error.response.data.errors;\n                    _this.$emit('message', error.response.data.message || 'Error adding city');\n                } else {\n                    _this.$emit('message', 'Network error. Please try again.');\n                }\n            });\n        }\n    }\n}\n</script>\n\n<style scoped>\n.testi-popup-cont{\n    width: 100%;\n    height: 100%;\n    position: fixed;\n    background: rgba(0, 0, 0, 0.50);\n    z-index: 999999999999999;\n    top: 0;\n    left: 0;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n.testi-form-cont{\n    border-radius: 8.388px;\n    background: #FFF;\n    box-shadow: 0px 3.35501px 50.3252px 0px rgba(0, 0, 0, 0.25);\n    width: 100%;\n    padding: 30px;\n    max-width: 620px;\n}\n.testi-form-head {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n}\n.testi-form-head > div {\n    color: #000;\n    font-family: Poppins;\n    font-size: 20px;\n    font-style: normal;\n    font-weight: 600;\n    line-height: normal;\n    display: flex;\n    flex-direction: column;\n}\n.testi-form-head > div > span:nth-child(2){\n    margin-top: 5px;\n    display: block;\n    width: 45px;\n    height: 6px;\n    background: #C4456F;\n}\n.testi-form-que{\n    color: #000;\n    font-family: Poppins;\n    font-size: 18px;\n    font-style: normal;\n    font-weight: 400;\n    line-height: normal;\n}\n.testi-form-que > span{\n    font-weight: 600;\n}\n.testi-form-cont textarea, .testi-form-cont input {\n    border-radius: 7px;\n    border: 0.8px solid #A4A4A4;\n    padding: 10px;\n    background-color: white !important;\n    width: 100%;\n    color: #898989;\n    font-family: Poppins;\n    font-size: 15px;\n    font-style: normal;\n    font-weight: 400;\n    line-height: 165%;\n}\n.testi-form-cont textarea:hover, .testi-form-cont input:hover{\n    background-color: white !important;\n}\n.testi-form-cont > form button{\n    border: none;\n    outline: none;\n    margin-bottom: 10px;\n    border-radius: 11px;\n    background: #461952;\n    padding: 18px 33px;\n    color: #FFF;\n    font-family: Mulish;\n    font-size: 12.581px;\n    font-style: normal;\n    font-weight: 800;\n    line-height: normal;\n    display: inline-block;\n    margin-left: auto;\n}\n.errMsg {\n    color: red;\n    font-size: 12px;\n    display: block;\n    margin-top: 5px;\n}\n.justify-end {\n    display: flex;\n    justify-content: flex-end;\n    margin-top: 15px;\n}\n.multi-select {\n    border-radius: 7px;\n    border: 0.8px solid #A4A4A4;\n    background-color: white !important;\n}\n</style>\n\n\n\n"],"sourceRoot":""}]);
